import React, {Component} from "react";
import "./form.css";

class Main extends Component {
    state = {T: 6};


    updateState = (values) => {
        this.setState({...this.state, ...values});
    };

    render() {
        return (
            <main className="main">
                <h1 className="gray roboto300">Main</h1>
                <Config config={this.state} update={this.updateState}/>
                <TreeView config={this.state}/>
            </main>
        );
    }
}

function updater(update) {
    return (e) => {
        const T = parseInt(e.target.value, 10);
        e.preventDefault();
        if (T > 3 && T < 12) {
            update({ T });
        }
    }
}
function Config({config, update}) {
    return (
        <form>
            <label className="label">
                <span>T:</span>
                <input type="number" placeholder="T" value={config.T}
                       onChange={updater(update)}/>
            </label>
        </form>
    );
}

class Node {
    constructor(x, y, left, right) {
        this.x = x;
        this.y = y;
        this.children = left && right ? [right, left] : [];
    }
}
function generateTree2(depth) {
    const reversedLevels = new Array(depth).fill(0).map(() => []);

    reversedLevels[0] = new Array(Math.pow(2, depth)).fill(0).map((_, i) => new Node(10 + i * 20, 10 + depth * 40));

    for (let level = 1; level <= depth; level++) {
        const base = reversedLevels[level - 1];
        const thisLevel = [];
        for (let i = 0; i < base.length; i += 2) {
            const left = base[i];
            const right = base[i + 1];
            thisLevel.push(new Node(
                (left.x + right.x) / 2,
                left.y - 40,
                left,
                right
            ));
        }
        reversedLevels[level] = thisLevel;
    }

    return reversedLevels[depth][0];
}

function treeMap(tree, fn) {
    let stack = [tree];
    const output = [];
    while (stack.length > 0) {
        const element = stack.pop();
        output.push(fn(element));
        stack = [...stack, ...element.children];
    }

    return output;
}

function TreeView({config}) {
    const {T} = config;
    const leafNodeCount = Math.pow(2, T);
    const xSize = (leafNodeCount * 20);
    const ySize = (T * 40) + 20;
    const viewBox = `0 0 ${xSize} ${ySize}`;
    const tree = generateTree2(T);

    const nodes = treeMap(tree, ({x, y}) => <SvgNode x={x} y={y}/>);
    const edges = treeMap(tree, (node) => {
        if (node.children.length !== 2) {
            return [];
        }

        return [
            <SvgEdge coors={[node.x, node.y, node.children[0].x, node.children[0].y]}/>,
            <SvgEdge coors={[node.x, node.y, node.children[1].x, node.children[1].y]}/>
        ]
    }).reduce((list, edges) => [...list, ...edges], []);


    return (
        <svg viewBox={viewBox}>
            {nodes}
            {edges}
        </svg>
    );
}

function SvgNode({x, y}) {
    return (
        <circle cx={x} cy={y} r="6" fill="black"/>
    );
}
function SvgEdge({coors}) {
    return (
        <line x1={coors[0]} y1={coors[1]} x2={coors[2]} y2={coors[3]} stroke="black" strokeWidth={2}/>
    );
}
function SvgText({text, coors}) {
    return null;
}

export default Main;


